import React, {Component} from 'react';
import "../../node_modules/bootstrap/dist/css/bootstrap.min.css"
class DemoNavbar extends Component {
    render() {
        return (
            <>
                <nav className="navbar navbar-expand-lg py-4" style={{background: "white"}}>
                    <div className="navbar-brand font-weight-bold">
                        <a href="" className=""> PORTFOLIO</a>
                    </div>
                    <div className="ml-auto">
                        <button className="navbar-toggler ml-auto" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"><i className="fa fa-bars">bar</i></span>
                        </button>
                    </div>

                    <div className=" collapse  font-weight-bold  navbar-collapse " id="navbarSupportedContent">
                        <ul className="navbar-nav ml-auto" >
                            <li className="nav-item"><a href="#" className="nav-link" style={{color: "black"}}>Home</a></li>
                            <li className="nav-item"><a href="#" className="nav-link" style={{color: "black"}}>About</a></li>
                            <li className="nav-item"><a href="#" className="nav-link" style={{color: "black"}} >Service</a></li>
                            <li className="nav-item"><a href="#" className="nav-link" style={{color: "black"}}>Portfolio</a></li>
                            <li className="nav-item dropdown">
                                <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                    Pages
                                </a>
                                <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                                    <a className="dropdown-item" href="#">Blog</a>
                                    <a className="dropdown-item" href="#">Blog Details</a>
                                    <div className="dropdown-divider"></div>
                                    <a className="dropdown-item" href="#">Elements</a>
                                    <a className="dropdown-item" href="#">Portfolio Design</a>

                                </div>
                            </li>
                            <li className="nav-item"><a href="#" className="nav-link" style={{color: "black"}}>Contact</a></li>
                            <li className="nav-item"><a href="#" className="btn btn-danger" >Get free Consultant</a></li>


                        </ul>
                    </div>
                </nav>
            </>
        );
    }
}

export default DemoNavbar;